.contact .content .watermark {
  position: absolute;
  font-weight: 700;
  font-size: 90px;
  bottom: 10px;
  right: -40px;
  line-height: 1em;
  z-index: -1;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  -webkit-animation: watermark 3s cubic-bezier(0.165, 0.84, 0.44, 1);
          animation: watermark 3s cubic-bezier(0.165, 0.84, 0.44, 1);
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  background: #f7f7f7;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

